"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useCart } from "@/context/CartContext";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import Image from "next/image";

interface CheckoutForm {
  nama: string;
  email: string;
  noHp: string;
  kabupaten: string;
  pembayaran: string;
}

const PAYMENT_METHODS = [
  { id: "transfer", label: "Transfer Bank" },
  { id: "cod", label: "Bayar di Tempat (COD)" },
  { id: "ewallet", label: "E-Wallet" },
];

const KABUPATEN = [
  "Jakarta Pusat",
  "Jakarta Utara",
  "Jakarta Barat",
  "Jakarta Selatan",
  "Jakarta Timur",
  "Bogor",
  "Depok",
  "Tangerang",
  "Bekasi"
];

export default function CheckoutPage() {
  const router = useRouter();
  const { items, totalPrice, clearCart } = useCart();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [form, setForm] = useState<CheckoutForm>({
    nama: "",
    email: "",
    noHp: "",
    kabupaten: "",
    pembayaran: "",
  });

  if (items.length === 0) {
    router.push("/cart");
    return null;
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1500));

    // Show success message and clear cart
    alert("Pesanan berhasil dibuat! Terima kasih telah berbelanja.");
    clearCart();
    router.push("/");
  };

  const updateForm = (key: keyof CheckoutForm, value: string) => {
    setForm(prev => ({ ...prev, [key]: value }));
  };

  const isFormValid = Object.values(form).every(value => value.trim() !== "");

  return (
    <div className="container max-w-2xl mx-auto px-4 py-4 pb-20">
      <h1 className="text-2xl font-bold text-gray-900 mb-6">Konfirmasi Pesanan</h1>

      {/* Order Summary */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-100 p-4 mb-6">
        <h2 className="font-semibold text-gray-900 mb-4">Ringkasan Pesanan</h2>
        <div className="space-y-4">
          {items.map((item) => (
            <div key={item.product.id} className="flex gap-3">
              <div className="relative w-16 h-16 flex-shrink-0">
                <Image
                  src={item.product.image}
                  alt={item.product.name}
                  fill
                  className="object-cover rounded-md"
                />
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="font-medium text-sm text-gray-900">{item.product.name}</h3>
                <p className="text-sm text-gray-500">
                  {item.quantity} x Rp {item.product.price.toLocaleString('id-ID')}
                </p>
              </div>
              <div className="text-right">
                <p className="font-medium text-green-600">
                  Rp {(item.quantity * item.product.price).toLocaleString('id-ID')}
                </p>
              </div>
            </div>
          ))}
        </div>
        <div className="mt-4 pt-4 border-t border-gray-100">
          <div className="flex justify-between">
            <span className="font-medium">Total</span>
            <span className="font-bold text-green-600">
              Rp {totalPrice.toLocaleString('id-ID')}
            </span>
          </div>
        </div>
      </div>

      {/* Checkout Form */}
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Nama Penerima
          </label>
          <Input
            required
            value={form.nama}
            onChange={(e) => updateForm("nama", e.target.value)}
            placeholder="Masukkan nama penerima"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Email
          </label>
          <Input
            required
            type="email"
            value={form.email}
            onChange={(e) => updateForm("email", e.target.value)}
            placeholder="Masukkan email"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Nomor HP
          </label>
          <Input
            required
            type="tel"
            value={form.noHp}
            onChange={(e) => updateForm("noHp", e.target.value)}
            placeholder="Masukkan nomor HP"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Kabupaten
          </label>
          <Select
            value={form.kabupaten}
            onValueChange={(value) => updateForm("kabupaten", value)}
          >
            <SelectTrigger>
              <SelectValue placeholder="Pilih kabupaten" />
            </SelectTrigger>
            <SelectContent>
              {KABUPATEN.map((kab) => (
                <SelectItem key={kab} value={kab}>
                  {kab}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Metode Pembayaran
          </label>
          <Select
            value={form.pembayaran}
            onValueChange={(value) => updateForm("pembayaran", value)}
          >
            <SelectTrigger>
              <SelectValue placeholder="Pilih metode pembayaran" />
            </SelectTrigger>
            <SelectContent>
              {PAYMENT_METHODS.map((method) => (
                <SelectItem key={method.id} value={method.id}>
                  {method.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <Button
          type="submit"
          className="w-full bg-green-600 hover:bg-green-700 text-white mt-6"
          disabled={!isFormValid || isSubmitting}
        >
          {isSubmitting ? "Memproses..." : "Buat Pesanan"}
        </Button>
      </form>
    </div>
  );
}
